body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar{display:flex;justify-content:space-between;align-items:center;margin:0px 8px}.navbar h1{color:#18379B;font-size:35px;margin:3px;margin-left:10px}.navbar__left a{padding:6px;margin-left:3px;margin-right:10px;font-weight:700}.navbar__left a:hover{color:#18379B;transition:0.5s ease-in-out}footer{display:flex;justify-content:center;margin-top:10px;margin-bottom:10px}footer a{margin-top:20px}.fourohfour{display:grid;margin:3px}.fourohfour h1{color:#18379B;font-size:50px;place-self:center;border:1px solid black;padding:40px;border-radius:20px;text-align:center}.fourohfour h1 p a{color:inherit;text-decoration:underline}.profile{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;margin-top:20px;min-height:78vh}.profile__headshot{max-width:600px;max-height:400px;border-radius:10px}.profile__bio--text{font-size:20px;margin-left:20px;max-width:500px;line-height:1.6}.profile__bio--text span{color:#18379B;font-weight:500}.profile__bio--text a{text-decoration:underline;color:grey;font-weight:500}.profile__bio--text a:hover{text-decoration:underline;color:#18379B;font-weight:500}.project-grid{display:grid;margin-top:10px;justify-content:center;max-width:1080px;margin:auto;padding:0 1em}.project-grid__tile{display:flex;color:grey;justify-content:center;align-content:center;grid-row-gap:8px;row-gap:8px;border-bottom:1px solid grey;padding:1em 0}.project-grid__tile:nth-child(odd) .project-grid__tile--image{order:2}.project-grid__tile--image{display:grid;justify-items:center;grid-row-gap:8px;row-gap:8px;box-shadow:0px 0px 30px 0px #0000001a;margin:0 1em}.project-grid__tile--image:hover{color:#18379B;font-weight:700}.project-grid__tile--image img{max-width:600px;border-radius:8px}.project-grid__tile--text{display:grid;align-content:center;margin-left:3px}.project-grid__tile--text h2{margin-bottom:0}.project-grid__tile--text h2:hover{color:#18379B}.tech-header{margin-left:18px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:grey}.tech-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(100px, 1fr));justify-items:center;grid-row-gap:50px;row-gap:50px;grid-column-gap:50px;-webkit-column-gap:50px;column-gap:50px;padding:10px;max-width:1080px}.tech-grid i{font-size:100px;margin-left:100px;margin-right:100px}body{font-family:"Montserrat",sans-serif}.fab,.fas{color:grey;font-size:34px;padding-left:18px}.fab:hover,.fas:hover{color:#18379B}a,a:hover,a:active,a:visited{color:grey;text-decoration:none}h1,h2,h3,h4{font-family:"Bodoni Moda",sans-serif}

